}
.cms__page__content {
    margin-left: 240px;
    overflow: hidden;
    padding: 0 15px 5px 15px;
    min-height: 100vh;
    margin-top: 70px;
    transition-duration: 0.2s;
    background: #f6f6f6;
}
.cms-enlarged .cms__page__content {
    margin-left: 70px !important;
}
.add__product label {
