}
.add__product__form__title {
    font-weight: 600;
    display: block;
}
.col-form-label {
    line-height: 18px;
}
.text__small {
    font-size: 13px;
}

.cms__title {
    font-weight: 600;
    color: #39b54a;
    text-align: center;
}
.cmsHome__title {
    text-align: center;
    color: #39b54a;
    padding: 10px 0;
    background: #fff;
    font-weight: 600;
}
.cms__subTitle {
    font-weight: 600;
    text-align: center;
    color: darkslateblue;
    padding-bottom: 25px;
}
