}

.product__color__wrapper {
    display: flex;
    width: 100%;
    border: 1px solid #d0cbcb;
    border-radius: 5px;
}
.product__color__input {
    border: none;
    outline: none;
    width: 75%;
    margin-left: 5px;
}
.product__color__add {
    background: #fff;
    border-radius: 5px;
    font-size: 14px;
    border: 1px solid #999;
    height: 33px;
    margin: auto;
    padding: 5px;
}

.all__box__wrapper {
    display: flex;
    margin-bottom: 16px;
}

.box__wrapper {
    display: flex;
    align-items: center;
}

.print{
    background: #39b54a;
    color: #fff;
    padding: 5px 10px;
    margin-bottom: 8px;
    border-radius: 3px;
    display: inline-block;
    cursor: pointer;
}

.printWrapper{
    margin-top: 15px;
    display: flex;
    justify-content: space-between;
    width: 100%;
}
.printWrapper input{
    outline: none;
    padding-left: 10px;
    border: 1px solid #333;
}

@media (max-width: 575.98px) {
