    display: none;
}
.cms-enlarged .cms__sidebar__menu__link__right {
    display: none;
}
.cms-enlarged .cms__sidebar__menu__link {
    min-height: 40px;
    transition: none;
    transition-duration: 0.2s;
    padding-left: 15px;
}

