    right: 0px;
    z-index: 1111;
    overflow-x: hidden;
    background-color: #fff;
    color: #333;
}
.cart-sidebar__close-icon {
    position: absolute;
    top: 50%;
    left: 0px;
    display: grid;
    align-items: center;
    justify-content: center;
    color: #fff;
    height: 100px;
    width: 20px;
    background: #428b46;
    z-index: 100;
    cursor: pointer;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
}
.cart-sidebar__close-icon:hover {
    background: #1d6621;
}
.cart-sidebar__hide {
    width: 0px !important;
}
.cart__side__header {
    padding: 5px 0;
}
.cart__side__header svg {
    width: 28px;
    color: #dc1e1e;
    background-color: #fff;
    height: 100%;
    margin-right: 8px;
    cursor: pointer;
    margin-left: 5px;
}
.cart-sidebar__header-content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 20px;
    color: #f2f2f2;
    position: relative;
}
.cart-sidebar__headerIcon,
.cart-sidebar__header-close-icon {
    font-size: 25px;
    margin-top: -10px;
}
.cart-sidebar__header-total-product {
    position: absolute;
    font-size: 12px;
    top: 23px;
    left: 28px;
    font-weight: 700;
}

.cart__side__img {
    display: flex;
    align-content: center;
    align-items: center;
    padding-right: 4px;
    padding-left: 14px;
}
.cart__side__img img {
    width: 60px;
    height: 60px;
    margin: 0 auto;
}

.cart__side__details {
    display: flex;
    align-content: center;
    align-items: center;
    padding-right: 4px;
    padding-left: 2px !important;
}
.cart__side__item__name {
    margin-bottom: 8px;
    font-size: 12px;
    line-height: 13px;
    font-weight: 600;
    padding: 0 !important;
    display: flex;
    align-content: center;
    align-items: center;
}
.cart__side__item__brief {
    font-size: 12px;
    line-height: 13px;
    padding: 0;
    display: flex;
    align-content: center;
    align-items: center;
    padding-right: 4px;
}
.cart__side__quantity {
    padding: 0;
    display: flex;
    align-content: center;
    align-items: center;
    padding-right: 4px;
}
.center-flex{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
.item__quantity {
    display: block;
    font-size: 13px;
    font-weight: bold;
}
.item__icon {
    color: #39b54a;
    cursor: pointer;
    display: block;
    font-size: 22px;
    font-weight: 600;
    transition: all 0.3s;
}
.item__icon:hover {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}
.cart__side__price {
    display: flex;
    justify-content: center;
    align-items: center;
}
.item__price {
    font-weight: 600;
    font-size: 14px;
}
.item__price__reduced {
    font-weight: 600;
    font-size: 14px;
    text-decoration: line-through;
}
.cart__side__remove {
    display: flex;
    align-items: center;
}
.cart__side__remove svg {
    color: rgb(172, 26, 26);
    cursor: pointer;
    transition: all 0.3s;
}
.cart__side__remove svg:hover {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}

