.category__section {
    clear: both;
    display: -webkit-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
}
.category__section hr {
    margin-top: 12px;
    float: left;
    height: 1px;
    flex-grow: 4;
    -webkit-flex-grow: 4;
    background-color: #666;
    border-bottom: none;
}

.category__section .name {
    flex-grow: 1;
    -webkit-flex-grow: 1;
    float: left;
    font-size: 23px;
    color: #666;
    padding-bottom: 10px;
    text-align: center;
    margin-top: -6px;
}
.filter__link__1 {
    color: #333;
    text-decoration: none;
    font-weight: 600;
    font-size: 15px;
}

.category__image__wrapper {
    width: 100%;
    background-color: #fff;
    position: relative;
    cursor: pointer;
    text-align: center;
}
.sub__category__link {
    margin: 5px;
    padding: 5px;
    box-sizing: content-box;
    border-radius: 4px;
    text-decoration: none;
    text-align: center;
}
.category__image__wrapper img {
    max-width: 100%;
    max-height: 100%;
}

.category__image__wrapper .name {
    font-size: 16px;
    text-align: center;
}
